<template>
 <header @click="changeTitle">
   <h1>{{title1}}{{title}}</h1>
 </header>
</template>

<script>
  export default {
    name: "app-header",
    props:{
      title:{
        type:String,
        required:true
      }
    },
    data(){
      return{
        title1:"Vue.js Demo"
      }
    },methods:{
      changeTitle:function () {
       //this.title="修改";
       this.$emit("titleChanged","子项父组件传值");//注册titlechanged事件，并把值传进去
      }
    }
  }
</script>

<style scoped>
header{
  background: lightgreen;
  padding: 10px;
}
  h1{
    color: #222;
    text-align: center;
  }
</style>



