<template>
  <div class="users">
    <h1>User page</h1>
    <ul>
      <li v-for="user in users" >
        <h2>{{user.name}}</h2>
        <h3 >{{user.position}}</h3>
      </li>
    </ul>
    <button @click="deleteUser">删除</button>
  </div>
</template>
<!--
传值 string number boolean
引用 array object
-->
<script>
  export default {
    name: "users",
    //子组件须通过props 获取父组件的数据
    props: {
      users: {
        type: Array,//数据类型
        required: true//是否规格
      }
    },
    data() {
      return {}
    },methods:{
      deleteUser:function () {
        this.users.pop();
      }
    }
  }
</script>

<style scoped>
  .users {
    width: 100%;
    max-width: 1200px;
    margin: 40px auto;
    padding: 0 20px;
    box-sizing: border-box;
  }

  ul {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
  }

  li {
    flex-grow: 1;
    flex-basis: 200px;
    text-align: center;
    padding: 30px;
    border: 1px solid #222;
    margin: 10px;
  }

  h1 {
    color: #0000ff;
  }
</style>



