<template>
    <footer>
      <h1>{{copyright}}{{title}}</h1>
    </footer>
</template>

<script>
    export default {
        name: "app-footer",
      props:{
        title:{
          type:String,
          required:true
        }
      },
       data(){
          return{
            copyright:"copyright 2019 Vue.js Demo"
          }
       }
    }
</script>

<style scoped>
footer{
  background: #222;
  padding: 5px;
}
  h1{
    color: lightgreen;
    text-align: center;
  }
</style>



